import React from 'react'
import Poke_Ball from '../assets/img/Poke_Ball.png'

const Inicio = () => {
    return (
        <div className="container mt-5 text-center">
            <h1>Bienvenido al pokerList</h1>
            <p>Presione el boton de listado para ver el catalogo completo</p>
            <img className="img-fluid" width="200vw" src={Poke_Ball} alt=""/>
        </div>
    )
}
export default Inicio
