import React, { useEffect } from 'react'

const Listado = () => {

    const [listadoPoke, setListadoPoke] = React.useState([])

    const [detallePoke, setDetallePoke] = React.useState()
    const [habiUno, SetHabiUno] = React.useState('')
    const [habiDos, SetHabiDos] = React.useState('')
    const [imgPoke, setImgPoke] = React.useState('')
    var urlDetalle;

    useEffect(() => {
        (async () => {
            const data = await fetch(`https://pokeapi.co/api/v2/pokemon/`)
            const list = await data.json()
            const poke = list.results
            setListadoPoke(poke)
        })();
    }, []);



    const ObtenerDatos = async (e) => {
        e.preventDefault()
        const data = await fetch(`https://pokeapi.co/api/v2/pokemon/1`)
        const resultado = await data.json()
        setDetallePoke(resultado)
        setImgPoke(resultado.sprites.front_default)
        SetHabiUno(resultado.abilities[0].ability.name)
        SetHabiDos(resultado.abilities[1].ability.name)
    }



    return (
        <div className="container text-center">
            <div className="row">
                <div className="col-md-12">
                    <h1 className="mt-4 mb-3">
                        Listado de pokemones
                    </h1>
                </div>
                <div className="col-md-12">

                <ul className="list-group list-group-flush ">

                    {
                        listadoPoke.map(item => (
                                <li className="list-group-item text-uppercase text-left"><strong>Nombre:</strong> {item.name} <span className="float-right"><strong>URL:</strong> {item.url}</span> </li>
                        


                            // <div className="container col-md-3">
                            //     <div className="card mx-2 mb-3">

                            //         <div className="card-body">
                            //             <h5 className="card-title text-uppercase">{item.name}</h5>

                            //                 {/* <button
                            //                      className="btn btn-secondary " 
                            //                      onClick={ObtenerDatos}

                            //                 > Ver datalle (Proximamente)
                            //                 </button> */}

                            //         </div>

                            //     </div>
                            // </div>

                        ))
                    }
                </ul>
                </div>

            </div>

        </div>
    )
}

export default Listado
