import React from 'react'

const Buscador = () => {

    const [valor, setValor] = React.useState()
    const [pokemones, setPokemones] = React.useState('')
    const [imgPoke, setImgPoke] = React.useState('')
    const [imprimir,SetImprimir] = React.useState(false)
    const [habiUno,SetHabiUno] = React.useState('')
    const [habiDos,SetHabiDos] = React.useState('')


    const ObtenerDatos = async (e) => {
        e.preventDefault()
        const data = await fetch(`https://pokeapi.co/api/v2/pokemon/${valor}`)
        const resultado = await data.json()
        setPokemones(resultado)
        setImgPoke(resultado.sprites.front_default)
        SetHabiUno(resultado.abilities[0].ability.name)
        SetHabiDos(resultado.abilities[1].ability.name)

        SetImprimir(true)

    }

    return (
        <div className="container mt-3 text-center">
            <h2 className="mb-3">Buscador</h2>
            <div className="container">
                {/* Formulario de solucitud de pokemon */}
                <form onSubmit={ObtenerDatos}>
                    <div className="form-group">
                        <input
                            className="form-control"
                            placeholder="Ingrese el numero del pokemon"
                            onChange={(e) => (setValor(e.target.value))}
                            value={valor}
                            type="number" />
                    </div>
                    <div className="form-group">
                        <button className="btn btn-primary btn-block" type="submit">
                            Buscar
                        </button>
                    </div>
                </form>

                {/* Resultado */}

                <div className={imprimir===true ? 'visible':'invisible'}>
                    <div className="col-md-5 container mt-5">
                        <div className="card">
                            <img src={imgPoke}  className="card-img-top img-thumbnail" alt="..." />
                            <div className="card-body">
                                <h5 className="card-title text-uppercase">{pokemones.name}</h5>
                                <p className="card-text text-left text-uppercase">
                                    <strong>Experiencia base: </strong> {pokemones.base_experience} <br/>
                                    <strong>Peso: </strong> {pokemones.weight} <br/>
                                    <strong>Altura: </strong> {pokemones.height} <br/>
                                    <strong>Habilidad 1: </strong> {habiUno} <br/>
                                    <strong>Habilidad 2: </strong> {habiDos}


                                </p>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </div>

    )
}

export default Buscador
